  /* CSS for styling range finder (for PWM selection) - https://css-tricks.com/styling-cross-browser-compatible-range-inputs-css/ */
  input[type=range] {
    -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
    width: 100%; /* Specific width is required for Firefox. */
    background: transparent; /* Otherwise white in Chrome */
  }
  
  input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
  }
  
  input[type=range]:focus {
    outline: none; /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */
  }
  
  input[type=range]::-ms-track {
    width: 100%;
    cursor: pointer;
  
    /* Hides the slider so custom styles can be added */
    background: transparent; 
    border-color: transparent;
    color: transparent;
  }

  /* Special styling for WebKit/Blink */
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: 1px solid #000000;
  height: 10px;
  width: 10px;
  border-radius: 3px;
  background: var(--bgColor4);
  cursor: pointer;
  margin-top: -14px; /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; /* Add cool effects to your sliders! */
}

/* All the same stuff for Firefox */
input[type=range]::-moz-range-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 10px;
  width: 10px;
  border-radius: 3px;
  background: var(--bgColor4);
  cursor: pointer;
}

/* All the same stuff for IE */
input[type=range]::-ms-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 10px;
  width: 10px;
  border-radius: 3px;
  background: var(--bgColor4);
  cursor: pointer;
}

/* Handle Pin Selector Styling on hover*/
.clickable:hover{
  stroke:black;
  stroke-width: 3.5;
}

g.pinSelector:hover text{
  fill:white;
}

g.pinSelector:hover rect{
  fill:black;
}

g.cantControl:hover text{
  fill:white;
}

g.cantControl:hover rect{
  fill:#d4ae1b;
}


.pianoKey{
  fill:white;
}

.pianoKey:hover{
  stroke-width:3.5;
  fill:yellow;
}

.pianoSharpKey{
  fill:black;
}

.pianoSharpKey:hover{
  stroke-width:3.5;
  fill:yellow;
}

/* Loading Bar*/

.loader {
  border: 8px solid #f3f3f3; /* Light grey */
  border-top: 8px solid #236467; /* Blue */
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 2s linear infinite;
  float:left;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loadingDone {
  border: 8px solid #236467; /* Light grey */
  background-color: #236467;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  float:left;
}

.loaderText{
  float:left;
  margin-top:-15px;
  margin-left:5px;
  margin-right:10px;
}

.menu_large{
  font-size:2em;
  font-weight:bold;
}
.menu_medium{
  font-size:1.5em;
  font-weight:bold;
}

.collapsing {
  -webkit-transition: none;
  transition: none;
  display: none;
}

#gitLogo{
  fill:--fontColor1;
}

a:hover #gitLogo{
  fill: --bgColor1;
}

.stepSelector{
  cursor:pointer; 
  font-size:1.5em;
  color:white;
  background-color:#343a40;
  border-color:white;
  font-weight:bold;
}

.stepSelector:hover{
  background-color:#6c757d;
}


